
import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class PopOver extends Vue {
  @Prop() private text!: string;
  showingPopOver: boolean = false;

  showPopOver() {
    this.showingPopOver = true;
  }

  hidePopOver() {
    this.showingPopOver = false;
  }

  handleHover() {
    this.showPopOver();
  }

  handleLeave() {
    this.hidePopOver();
  }
}
